/**
 * WordPress Dependencies
 */
import { __ } from '@wordpress/i18n';
import { addFilter } from '@wordpress/hooks';
import { createHigherOrderComponent } from '@wordpress/compose';
import { Fragment } from '@wordpress/element';
import { InspectorAdvancedControls } from '@wordpress/block-editor';
import {
	TextControl,
	ToggleControl,
	SelectControl,
} from '@wordpress/components';

/**
 * Internal Dependencies
 */

import { FlexboxControl } from '../_shared';

const BLOCKNAME = 'core/template-part';
const BLOCKIDENTIFIER = 'prc-core-block-library/template-part';

/**
 * @NOTE: Modify default settings on core/template-part block. Sometimes defining attributes via PHP is not enough and this is one of those times.
 * I'm assuming this is because this block is still quite experimental and the API is not fully fleshed out yet, re: Site Editor vs Post Editor.
 *
 * @param {*} settings
 * @param {*} name
 * @returns
 */
addFilter('blocks.registerBlockType', BLOCKIDENTIFIER, (settings, name) => {
	if (BLOCKNAME !== name) {
		return settings;
	}
	const s = settings;
	console.log('Tempalte Part -> settings', settings);
	if ( settings && !settings['__experimentalLayout'] ) {
		settings['__experimentalLayout'] = {
			allowSwitching: true,
			allowInheriting: true,
			allowEditing: true,
			default: {
				type: 'flex',
			}
		};
	}
	return settings;
});

addFilter(
	'editor.BlockEdit',
	BLOCKIDENTIFIER,
	createHigherOrderComponent(
		(BlockEdit) =>
			function TemplatePartAdvancedControls(props) {
				const { name, attributes, setAttributes, clientId } = props;
				if (BLOCKNAME !== name) {
					return <BlockEdit {...props} />;
				}

				console.log('core/template-part', props);
				// Can we get the attribute and add style to props??
				props.style = {
					display: 'flex',
				}

				return (
					<Fragment>
						<BlockEdit {...props} />
						<FlexboxControl
							attributes={attributes}
							setAttributes={setAttributes}
							clientId={clientId}
						/>
					</Fragment>
				);
			},
		'witTemplatePartlockAdvancedControls',
	),
	21,
);
