/**
 * WordPress Dependencies
 */
import { __ } from '@wordpress/i18n';
import { Fragment } from '@wordpress/element';
import { InspectorAdvancedControls } from '@wordpress/block-editor';
import {
	TextControl,
	ToggleControl,
	SelectControl,
	CardDivider,
} from '@wordpress/components';

function FlexboxControl({ attributes, setAttributes, clientId = false }) {
	if (false === clientId) {
		console.warn('<FlexboxControl/> requires a clientId prop.');
	}
	console.log('FlexboxControls', attributes, clientId);

	const { isFlexbox } = attributes;

	return (
		<InspectorAdvancedControls>
			<CardDivider />
			<ToggleControl
				label={__('Enable Flexbox', 'prc-cbl')}
				help={__('Enable CSS flexbox for this HTML element', 'prc-cbl')}
				checked={isFlexbox}
				onChange={() => {
					setAttributes({ isFlexbox: !isFlexbox });
				}}
			/>
		</InspectorAdvancedControls>
	);
}

export default FlexboxControl;
